.profile__name {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media screen and (max-width: 425px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    grid-area: 1/1/2/3;
  }
}
