.header__menu {
  display: flex;
  column-gap: 24px;

  box-sizing: border-box;
}
@media screen and (max-width: 425px) {
  .header__menu {
    flex-direction: column;
    align-items: center;

    row-gap: 18px;

    padding: 40px 0;

    border-bottom: 1px solid #545454;

    display: none;
  }
}
